<script>
import PostPage from './PostPage'

export default {
  components: {
    PostPage,
  },
  created: function(){
    if(window.scrollY > 200){
      window.scrollTo(0, 0)
    }
  },
  methods: {
    addActive(e){
      e.target.parentElement.parentElement.querySelectorAll('li').forEach(link => {
        link.children[0].classList.remove('active')
      });
      e.target.classList.add('active')
      e.preventDefault()
    }
  }
}
</script>

