<script>

export default {
  name: 'carrousel',
  data: function(){
    return {
      swiperOptions: {
        centeredSlides: true,
        loop: true,
        autoPlay: {
          delay: 2500,
          disableOnInteraction: false,
        },
      }
    }
  },
  methods: {
    addActive(e){
      e.target.parentElement.parentElement.querySelectorAll('li').forEach(link => {
        link.children[0].classList.remove('active')
      });
      e.target.classList.add('active')
      e.preventDefault()
    }
  }
}
</script>

