// import components
import Home from './components/Home'
import Category from './components/Category'
import CategoryPosts from './components/CategoryPosts'
import PostPage from './components/PostPage'
import Contact from './components/Contact'


// routes
const routes = [
    {path: '/', component: Home, name: 'home', props: false, children: []},
    {path: '/category', component: Category, name: 'category', props: false, children: [
        {path: 'politics', component: CategoryPosts, name: 'politics', props: true, children: [
            {path: ':id', component: PostPage, name: 'post', props: true, children: []},
        ]},
        {path: 'business', component: CategoryPosts, name: 'business', props: true, children: [
            {path: ':id', component: PostPage, name: 'post', props: true, children: []},
        ]},
        {path: 'health', component: CategoryPosts, name: 'health', props: true, children: [
            {path: ':id', component: PostPage, name: 'post', props: true, children: []},
        ]},
        {path: 'design', component: CategoryPosts, name: 'design', props: true, children: [
            {path: ':id', component: PostPage, name: 'post', props: true, children: []},
        ]},
        {path: 'sport', component: CategoryPosts, name: 'sport', props: true, children: [
            {path: ':id', component: PostPage, name: 'post', props: true, children: []},
        ]},
    ]},
    {path: '/contact', component: Contact, name: 'contact', children: []},
]

// export routes out to use
export default routes